<script>
  import Router from './../../router';
  import Tool from '../../configs/tool.js';
  export default {
    name: 'Header',
    props: ['title'],
    data(){
      return {
        terminal: Tool.getTerminal()
      }
    },
    methods: {
      goBack: function () {
        Router.back(1)
      }
    },
    created(){
    }
  }
</script>

<template>
  <header class="base_header" v-if="terminal==='msite'">
    <div class="header_box">
      <a href="javascript:void(0)" @click="goBack"><img src="../../assets/images/return.png" alt="" class="goBack"></a>
      <span class="cWhite">{{title}}</span>
    </div>
  </header>
</template>


