<script>
import Router from './../../router';
import Tool from '../../configs/tool.js';
export default {
  name: 'Footer',
  props: {
    currenttype: {
      type: Number,
      default() {
        return 1;
      }
    }
  },
  methods: {
    //单独点击不用分开多个函数
    footerclick(type) {
      this.pagetype = type;
      this.$router.push({ name:this.pagelist[type-1]});
    }
  },
  created() {

  },
  data() {
    return {
      pagetype:null,
      pagelist:['ExaminationRoom','Schedule','UserCenter']
    };
  },
  computed: {
    currentstate: function () {
      return this.pagetype||this.currenttype;
    }
  }
}
</script>

<template>
  <footer class="base_footer">
    <ul>
      <li @click="footerclick(1)">
        <dl v-if="this.currentstate===1">
          <dt class="footer_an_audition_clicked"></dt>
          <dd class="font_clicked">
            <em>面试</em>
          </dd>
        </dl>
        <dl v-else>
          <dt class="footer_an_audition"></dt>
          <dd>
            <em>面试</em>
          </dd>
        </dl>
      </li>

      <li @click="footerclick(2)">
        <dl v-if="this.currentstate===2">
          <dt class="footer_schedule_clicked"></dt>
          <dd class="font_clicked">
            <em>日程</em>
          </dd>
        </dl>
        <dl v-else>
          <dt class="footer_schedule"></dt>
          <dd>
            <em>日程</em>
          </dd>
        </dl>
      </li>
      <li @click="footerclick(3)">
        <dl v-if="this.currentstate===3">
          <dt class="footer_my_clicked"></dt>
          <dd class="font_clicked">
            <em>我的</em>
          </dd>
        </dl>
        <dl v-else>
          <dt class="footer_my"></dt>
          <dd>
            <em>我的</em>
          </dd>
        </dl>
      </li>
    </ul>
  </footer>
</template>


