<template>
    <form action="#" class="form"  @submit.prevent="formSubmit">
        <div class="search_warper" :style="{'width':width+'rem'}">
            <input type="search" :value="searchValue" class="search_input" v-on:focus="focusCallBack" v-on:keyup.enter="searchSubmit($event.target.value,$event)" v-bind:readonly="readonly" v-bind:v-focus="autofocus" v-focus v-bind:placeholder="placeholder" @click="searchClick"  />
            <!-- <input type="search" :value="searchValue" class="search_input" v-on:focus="focusCallBack" v-on:keyup.enter="searchSubmit($event.target.value)" v-bind:readonly="readonly" v-bind:v-focus="autofocus" v-focus v-bind:placeholder="placeholder" @click="searchClick" v-if="autofocus" /> -->
            <!-- <input type="search" :value="searchValue" class="search_input" v-on:focus="focusCallBack" v-on:keyup.enter="searchSubmit($event.target.value)" v-bind:readonly="readonly"  v-bind:placeholder="placeholder" @click="searchClick" v-else /> -->

        </div>
    </form>
</template>

<script>
export default {
    props: {
        placeholder: { type: String, default: () => '搜索考场/候选人' },
        searchClick: { type: Function, default: () => { } },
        searchSubmit: { type: Function, default: () => { } },
        focusCallBack: { type: Function, default: () => { console.log(1) } },
        autofocus: { type: Boolean, default: () => { return false; } },
        width: { type: Number, default: 12 },
        searchValueProp: { type: String, default: '' },
        searchValue: { type: String, default: '' },
        readonly:{type:Boolean, default:()=>{return false;}}

    },
    methods:{
        formSubmit(){
            return false;
        }
    },
    computed: {
        // searchValue:function(){
        //     return this.searchValueProp;
        // }
    }

}
</script>

<style lang="less" scoped>
@import (reference) "../../assets/css/base.less";
.form{
    display: inline-block;
}
.search_warper {
  display: inline-block;
  vertical-align: middle;
  background: @bgcolor;
  width: 480/@r;
  height: 60/@r;
  border-radius: 30/@r;
  box-sizing: border-box;
  padding: 0 0 0 20/@r;
  &:before {
    display: inline-block;
    width: 32/@r;
    height: 32/@r;
    background: url("../../assets/images/icon.png") -9/@r -9/@r;
    background-size: 640/@r 200/@r;
    vertical-align: middle;
    content: "";
  }
  .search_input {
    font-size: 26/@r;
    margin-left: 16/@r;
    width: 400/@r;
  }
}
</style>




